        width: 200px;
        // border: 1px solid #fa9551;
      }
      .btnSend {
        margin: 0 10px;
        border: 1px solid #377da5;
        background: #4092c2;
        color: white;
        padding: 0 5px;
        border-radius: 5px;
      }
    }
    p {
      color: #666666;
      border: none;
      span {
        font-weight: 900;
      }
    }

    .col-sm-10 {
      position: relative;
      height: 50px;

      .btn {
        position: relative;
        border: 1px solid #4092c2;
        height: 37px;
        left: 120px;
        top: -37px;
      }
    }
    .message-btn {
      position: absolute;
      bottom: 0;
      // margin: 0 0 0 -10px;
      button {
        background: #00aff0;
        margin: 0;
        font-size: 14px;
        width: 120px;
        padding: 0;
        height: 30px;
        border-radius: 6px;
        &:disabled,
        button[disabled] {
          background: lightgray;
          color: white;
          cursor: not-allowed;
          box-shadow: none;
          pointer-events: none;
          border: none;
        }
      }
    }
  }
